"use client"

import { TestSuiteList } from "@/app/components/test-suite-list"
import { Button } from "@/components/ui/button"
import { Plus } from "lucide-react"
import Link from "next/link"

interface PageProps {
  params: {
    id: string
  }
}

export default function TestSuitesPage({ params }: PageProps) {
  return (
    <div className="container mx-auto py-8">
      <div className="flex items-center justify-between mb-8">
        <h1 className="text-3xl font-bold">Test Suites</h1>
        <Link href={`/projects/${params.id}/test-suites/create`}>
          <Button>
            <Plus className="mr-2 h-4 w-4" />
            Create Test Suite
          </Button>
        </Link>
      </div>
      <TestSuiteList />
    </div>
  )
} 